import React, { Component } from "react";
import { Form } from "semantic-ui-react";
import { DateTimeInput } from "semantic-ui-calendar-react";
import * as Consts from "./Consts.js";

class FormCreateHazard extends Component {
  state = {};

  handleChange = (e, { value }) => this.setState({ value });

  render() {
    const { value } = this.state;
    return (
      <Form>
        <Form.Group widths="equal">
          <Form.Select
            fluid
            label="Hazard type"
            options={Consts.HazardTypes}
            placeholder="Hazard type"
          />
        </Form.Group>
        <Form.Group widths="equal">
          <Form.Select
            fluid
            search
            selection
            label="Location"
            options={Consts.Locations}
            placeholder="Enter hazard location"
          />
          <Form.Input
            label="OR enter manually"
            fluid
            placeholder="type address"
          />
        </Form.Group>
        <Form.TextArea
          label="Details"
          placeholder="Tell us more about the hazard..."
        />
        <Form.Button
          content="Upload Image or Video"
          labelPosition="left"
          icon="file"
          onClick={() => this.fileInputRef.current.click()}
        />
        <input
          ref={this.fileInputRef}
          type="file"
          hidden
          onChange={this.fileChange}
        />
        <Form.Checkbox label="Send Alert to local municipality" />
        <Form.Checkbox label="Anonymous report" />
        <DateTimeInput
          name="dateTime"
          placeholder="Date Time"
          value={this.state.dateTime}
          iconPosition="left"
          onChange={this.handleChange}
        />
        <Form.Group inline>
          <label>Size</label>
          <Form.Radio
            label="Small"
            value="sm"
            checked={value === "sm"}
            onChange={this.handleChange}
          />
          <Form.Radio
            label="Medium"
            value="md"
            checked={value === "md"}
            onChange={this.handleChange}
          />
          <Form.Radio
            label="Large"
            value="lg"
            checked={value === "lg"}
            onChange={this.handleChange}
          />
        </Form.Group>
        <Form.Button>Submit</Form.Button>
      </Form>
    );
  }
}

export default FormCreateHazard;
